import React from "react";
import Card from "../Card/Card";
import importAllImages from '../Helpers/ImportAllImages';
import restaurantsCardData from './RestaurantsData.json';

const restaurantImages = importAllImages(require.context('../PartnersLogos/Restaurants', false, /\.(svg)$/));

const Restaurants = () => {
    return (
        <>
            <h2>Restaurants</h2>
            <div className="section-container">
                {restaurantImages.map((restImage, index) => (
                    <Card
                        classAdded={"card-for-all"}
                        key={index} // Adding key prop for each Card component
                        image={restImage}
                        partner_name={restaurantsCardData.partner_name[index]}
                    />

                ))}
            </div>
        </>
    );
}

export default Restaurants;