import React, { useState, useEffect } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Card from "../../Card/Card";
import cardData from './TopPartners.json';
import importAllImages from '../../Helpers/ImportAllImages';

const images = importAllImages(require.context('../../PartnersLogos', false, /\.(svg)$/));

const Carousel = () => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const [isDesktop, setIsDesktop] = useState(false);

    useEffect(() => {
        const handleResize = () => {
            setIsDesktop(window.innerWidth > 768);
        };
        window.addEventListener('resize', handleResize);
        handleResize();
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: isDesktop ? 2 : 1, // Mostra 2 slide se isDesktop Ã¨ true, altrimenti mostra solo 1 slide
        slidesToScroll: 1,
        swipeToSlide: true,
        centerMode: true,
        focusOnSelect: true,
        autoplay: true,
        autoplaySpeed: 3000,
        afterChange: (index) => setCurrentIndex(index),
    };

    return (
        <Slider {...settings}>
            {images.map((image, index) => (
                <div key={index}>
                    <Card
                        classAdded={`carousel-item ${index === currentIndex ? 'active' : ''}`}
                        image={image}
                        partner_name={cardData.partner_name[index]}
                    />
                </div>
            ))}
        </Slider>
    );
};

export default Carousel;